.pricing {
  border: 1px solid white;
  margin: 1rem;
  padding: 1rem;
  border-radius: 1rem;

  div:first-child {
    display: flex;
    justify-content: start;
    align-items: baseline;
  }

  p {
    font-size: 50px;
    margin: 0;
    font-weight: 500;
  }


  button {
    font-family: 'Poppins', sans-serif;
    width: 100%;
    background-color: black;
    color: white;
    border: 1px solid white;
    font-size: 17px;
    padding: 0.8rem 1rem;
    border-radius: 10px;
    margin-top: 1rem;
    font-weight: 500;
    cursor: pointer;
  }

  button:hover {
    background-color: white;
    color: black;
  }

  &__feature {
    display: flex;
    align-items: center;
    img {
      width: 20px;
      height: 20px;
      margin-right: 5px;
    }    
  }

}